      <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
      <v-toolbar-title><v-icon>mdi-vector-curve</v-icon> Bezier NC Interpolation</v-toolbar-title>
    </v-toolbar>
    <v-content>
