          path:'/',
        },
        {
          title:"runing",
          icon:'mdi-cogs',
          path:'/test/'
        }
      ]
    }),
    props: {
      source: String
    },
    methods:{
      navigate(item){
        this.$router.push(item.path);
      }
    }
  }
</script>


