<template>
  <v-app>
     <v-toolbar dir="rtl" height="30px" >
        <v-toolbar-items align-center class="hidden-sm-and-down">
          <span class="subheading">نوع محصول در حال تولید : {{ config.productName }}</span>
          <span class="subheading"> @ واحد : {{config.unitName}}</span>

          
          
        </v-toolbar-items>
      </v-toolbar>
    <v-container fluid>
      <v-layout fluid row min-height="300px">
        <v-flex xs6 min-width="400" >
          <my-map style="z-index:-100;" :Config="config" :Part="activePart"></my-map>
        </v-flex>
        <v-flex xs6 max-height="240" dir="rtl" >
          <v-data-table
            :headers="headers"
            :items="last10Point"
            min-width="560"
            max-height="240"
            hide-actions
            class="elevation-1"
            dir="rtl"
          >
            <template slot="items" slot-scope="props" >
              <td @mouseover="setActivePart(props.item)" class="text-xs-right">{{ props.item.id }}</td>
              <td @mouseover="setActivePart(props.item)" class="text-xs-right">{{ props.item.date }}</td>
              <td @mouseover="setActivePart(props.item)" class="text-xs-right">{{ props.item.top }} <br> {{ props.item.topOv }} </td>
              <td @mouseover="setActivePart(props.item)" class="text-xs-right">{{ props.item.mid }} <br> {{ props.item.midOv }} </td>
              <td @mouseover="setActivePart(props.item)" class="text-xs-right">{{ props.item.bot }} <br> {{ props.item.botOv }} </td>
            </template>
          </v-data-table>
        </v-flex>
      </v-layout>    
     
      <v-layout fill-height fluid row wrap >
        <v-flex xs12>
          <highcharts width="100%" :options="chartOptionsAv" ref="highchartsAv" ></highcharts>
          <highcharts width="100%" :options="chartOptionsOv" ref="highchartsOv" ></highcharts>
          
        </v-flex>
      </v-layout>
    </v-container>
  </v-app>
</template>

<style scoped>

</style>

<script>
/* eslint-disable */
import myMap from '@/components/maps/Bush'

export default {
  data() {
    return {
      title: 'real-time ',
      activePart:null,
      pureData: [],
      config:null,
      points: [],
      chartType: 'Spline',
      seriesColor: '#6fcd98',
      headers: [{
          text: 'id',
          align: 'center',
          sortable: false,
          value: 'id'
        }, {
          text: 'date',
          value: 'date',
          align: 'center',
          sortable: false,
        },
        {
          text: 'top',
          value: 'top',
          align: 'center',
          sortable: false,
        },
        {
          text: 'Middle',
          value: 'mid',
          align: 'center',
          sortable: false,
        },
        {
          text: 'bottom',
          value: 'bot',
          align: 'center',
          sortable: false,
        }
      ],
      colorInputIsSupported: null,
      chartOptionsAv: {
        chart: {
          type: 'line',
          height:250,
        },
        xAxis:{
          visible:false,
          crosshair: true,
          gridLineWidth:1,
          gridLineColor:'#666666',
        },
        yAxis: {
          min:null,
			    max:null,
          title: {
            text: 'Average Diamiter'
          },
          plotLines: [{
            value: 0,
            width: 1,
            color: '#808080'
          }]
        },
        tooltip: {
          crosshairs: true,
          shared: true,
        },
        legend: {
          enabled: false
        },
        exporting: {
          enabled: false
        },
        title: {
          text: ""
        },
        series: [
          {
            title: "top average diamiter",
            name:"top average diamiter",
            data: [],
            color: '#ff652f'
          },
          {
            title: "middle average diamiter",
            name:"middle average diamiter",
            data: [],
            color: '#ffe400'
          },
          {
            title: "bottom average diamiter",
            name:"bottom average diamiter",
            data: [],
            color: '#14a76c'
          },
          {
            name: 'telorance',
            data: [],
            type: 'arearange',
            lineWidth: 1,
            lineColor: '#e45153',
            //     linkedTo: ':previous',
            color: '#c3ffb7',
            fillOpacity: 0.35,
            zIndex: 0,
             marker: {
               enabled: false
            }
          }
          
        ]
      },
      chartOptionsOv: {
        chart: {
          type: 'line',
          height:250,
        },
        xAxis:{
          visible:false,
          crosshair: true,
        },
        yAxis: {
          min:null,
			    max:null,
          title: {
            text: 'Ovality'
          },
          plotLines: [{
            value: 0,
            width: 1,
            color: '#808080'
          }]
        },
        tooltip: {
          crosshairs: true,
          shared: true,
        },
        legend: {
          enabled: false
        },
        exporting: {
          enabled: false
        },
        title: {
          text: ""
        },
        series: [
          {
            title: "top Ovality",
            name:"top average diamiter",
            data: [],
            color: '#ff652f'
          },
          {
            title: "middle Ovality",
            name:"middle average diamiter",
            data: [],
            color: '#ffe400'
          },
          {
            title: "bottom Ovality",
            name:"bottom average diamiter",
            data: [],
            color: '#14a76c'
          },
          {
            name: 'telorance',
            data: [],
            type: 'arearange',
            lineWidth: 1,
            lineColor: '#e45153',
            //     linkedTo: ':previous',
            color: '#c3ffb7',
            fillOpacity: 0.35,
            zIndex: 0,
             marker: {
               enabled: false
            }
          }
          
        ]
      }
    }
  },
  methods: {
    getUpdates(){
      this.$http.post('http://spcv4/index.php/Chart/getUpdatedRecords/'+this.$route.params.id, {
        limit: this.points.reverse()[this.points.length - 1].id
      }).then(response => {
        this.pureData = this.pureData.concat(response.body);
        console.log("getting updates");
      }, response => {
        // error callback
      });


    },
    setActivePart(item){
      console.log(item);
      this.activePart=item;
    },
    getConfig() {
      this.$http.get('http://spcv4/index.php/Chart/getFullSetting/'+this.$route.params.id).then(response => {
        //console.log(response);
        this.config = response.body;
      }, response => {
        // error callback
      });

      this.chartOptionsAv.yAxis.min=this.config.productMeanValue-0.02;
      this.chartOptionsAv.yAxis.max=this.config.productMeanValue+0.04;

      this.chartOptionsOv.yAxis.min=0;
      this.chartOptionsOv.yAxis.max=0.04;

      
    },
    log(item){
      console.log(item);
    }
  },
  watch: {
    points() {
      this.chartOptionsAv.series[0].data = Array.from(this.points, v => [v.index,v.top]);
      this.chartOptionsAv.series[1].data = Array.from(this.points, v => [v.index,v.mid]);
      this.chartOptionsAv.series[2].data = Array.from(this.points, v => [v.index,v.bot]);
      this.chartOptionsAv.series[3].data = Array.from(this.points , v => [v.index,v.avMin,v.avMax]);

      this.chartOptionsOv.series[0].data = Array.from(this.points, v => [v.index,v.topOv]);
      this.chartOptionsOv.series[1].data = Array.from(this.points, v => [v.index,v.midOv]);
      this.chartOptionsOv.series[2].data = Array.from(this.points, v => [v.index,v.botOv]);
      this.chartOptionsOv.series[3].data = Array.from(this.points , v => [v.index,v.ovMin,v.ovMax]);

      
    },
    pureData() {
      this.points = Array.from(this.pureData, (x, i) => {
        //var dat=Date(x.recordTime);
        //d.setUTCDate();
        //console.log("dateasdasdas",x.recordTime);
        //let j=gregorian_to_jalali(d.getYear(),d.getMonth(),d.getDay());
        return {
          index: i,
          id: Number(x.Id),
          top: Number(x.ParamTopAveDia),
          topOv: Number(x.ParamTopOvality),
          mid: Number(x.ParamMidAveDia),
          midOv: Number(x.ParamMidOvality),
          bot: Number(x.ParamBotAveDia),
          botOv: Number(x.ParamBotOvality),
          avMax:Number(x.MaxAccDia),
          avMin:Number(x.MinAccDia),
          ovMax:Number(x.MaxAccOvality),
          ovMin:Number(x.MinAccOvality),
          conic:Number(x.ParamConicity),
          coMax:Number(x.MaxAccConicity),
          //date:`${j[0]} / ${j[1]} / ${j[2]} ${d.getHours()} : ${d.getMinutes()} : ${d.getSeconds()} `,
          date:x.recordTime,        
        }
      });
      console.log(this.points);
    }
  },
   created() {
    this.$http.get('http://spcv4/index.php/Chart/getRecords/'+this.$route.params.id).then(response => {
      //console.log(response);
      this.pureData = response.body.reverse();
      //console.log(this.pureData);
      // this.points = Array.from(this.pureData, (x, i) => {
      //   return {
      //     index: i,
      //     id: Number(x.Id),
      //     top: Number(x.ParamTopAveDia),
      //     mid: Number(x.ParamMidAveDia),
      //     bot: Number(x.ParamBotAveDia)
      //   }
      // });
      // console.log(this.points);
      //console.log(this)

    }, response => {
      // error callback
    });
    this.getConfig();
    this.activePart=this.points[this.points.length-1];

    

  },
  mounted(){
    //setInterval(this.getConfig(),1000*60*15);
    this.interval=setInterval(this.getUpdates,10000);

  },
  components:{
    myMap,
  },
  computed:{
    last10Point(){
      let arr=[];//this.points.reverse();
      let tmp=this.points.reverse();
      let len=tmp.length;
      for(let i=0;i<6 ; i++){
        len--;
        arr.push(tmp[i]);
      }
      console.log("arr is ",arr);
      return arr;
      //return this.points.reverse();
    }
  }


}
</script>



