<template>
  <v-container fluid>
    <v-layout row wrap>
    <v-flex xs12 md9>
      <highcharts width="100%" :options="highchartConfig" ref="highcharts" ></highcharts>

    </v-flex>
    <v-flex xs12 md3>
      <v-layout column align-center>
        <v-slider
          v-model="req.resolotion"
          min="5"
          max="1500"
          :label="`${req.resolotion}`"
          hint="plot resolotion"
          persistent-hint
          append-icon="mdi-dots-horizontal"

        ></v-slider>
       <v-text-field
        label="X:"
        ref="xInput"
        v-model="tmpPoint.x"
        type="number"
        ></v-text-field>
        <v-text-field
        label="Y:"
        type="number"
        v-model="tmpPoint.y"
        @keyup.enter="addPoint(tmpPoint)"
        ></v-text-field>
        <v-text-field
        label="Z:"
        type="number"
        @keyup.enter="addPoint(tmpPoint)"
        v-model="tmpPoint.z"
        ></v-text-field>
        <v-layout row >
        <v-btn @click="addPoint(tmpPoint)">add</v-btn><v-icon @click="loadCurve">mdi-refresh</v-icon><v-icon @click="deleteAll">mdi-delete</v-icon>
        </v-layout>
      </v-layout>
      <v-slider
          v-model="req.rapidity"
          min="0.1"
          max="20"
          :label="`${req.rapidity}`"
          hint="rapidity"
          persistent-hint
          append-icon="mdi-speedometer"

        ></v-slider>
        <v-layout row wrap align-center justify-center>
        <v-btn @click="goPath" color="warning">Start</v-btn> <v-btn color="error">STOP</v-btn>
        </v-layout>
    </v-flex>

    </v-layout>
<v-data-table
    :headers="headers"
    :items="points"
    class="elevation-1"
  >
    <template slot="items" slot-scope="props">
      <td  class="text-xs-left">{{props.index}}</td>
      <td class="text-xs-left" ><v-icon @click="deletePoint(props)">mdi-delete-circle-outline</v-icon> <v-icon>mdi-circle-edit-outline</v-icon> </td>
      <td class="text-xs-left">{{ props.item.x }}</td>
      <td class="text-xs-left">{{ props.item.y }}</td>
      <td class="text-xs-left">{{ props.item.z }}</td>
    </template>
</v-data-table>
       <v-snackbar
      v-model="snackbar.toggle"
      :color="snackbar.status"
      :multi-line="true"
      :timeout="5000"
      top

    >
      {{ snackbar.message }}
      <v-btn
        dark
        flat
        @click="snackbar.toggle = false"
      >
        Close
      </v-btn>
    </v-snackbar>
    </v-container>
</template>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>
<script>
/* eslint-disable */
//import axios
const vm={
  data() {
    return {
      axios: require('axios'),
      headers: [{
          text: "index"
        },
        {
          text: "actions",

        },
        {
          text: 'X',
          align: 'left',
          sortable: false,
          value: 'x'
        },
        {
          text: 'Y',
          value: 'y',
          sortable: false,
          align: 'left',
        },
        {
          text: 'Z',
          value: 'z',
          sortable: false,
          align: 'left',
        },
      ],
      points:[],
      snackbar:{
        toggle:false,
        message:null,
        status:'ok'
      },
      req: {
        curve:"bezier",
        req:"plot",
        resolotion:100,
        rapidity:1,
        points: [[0,0,0],]
      },
      res:{
        curvePoints:[],
      },
      highchartConfig:{
      chart: {
          type: 'scatter',
          height:500,
          events: {
            click(e) {
                // find the clicked values and the series
                var x = Math.round(e.xAxis[0].value),
                    y = Math.round(e.yAxis[0].value),
                    series = this.series[0];
                    //vm.snackbar.message="I'm working on it"
                //console.log(vm);
                // Add it
                  //series.addPoint([x, y]);
                //console.log(vm);

                //vm.methods.addPoint({x:x,y:y,z:0})

            }
        }
        },
         xAxis: {
            gridLineWidth: 1,
            minPadding: 0.2,
            maxPadding: 0.2,
            title:{
              text:"x(mm)"
            }
        },
        yAxis: {
            title: {
                text: 'y(mm)'
            },
            minPadding: 0.2,
            maxPadding: 0.2,
            plotLines: [{
                value: 0,
                width: 1,
                color: '#808080'
            }]
        },
        tooltip: {
          crosshairs: true,
          shared: true,
        },
        legend: {
          enabled: true
        },

        title: {
          text: ""
        },
        plotOptions: {
          series: {
              lineWidth: 1,
              point: {
                  events: {
                      'click': function () {
                          if (this.series.data.length > 1) {
                              //this.remove();
                              console.log(this);
                          }
                      }
                  }
              }
          }
        },
        series: [
          { data: [],
            color: '#ff652f',
            name:"control points"
          },
          { data: [],
            color: '#0EAD69',
            name:"main curve",
          },
          { data: [],
            color: '#3BCEAC',
            name:"gone path"
          },
        ]
      },
      re: {
        Name: "hamid",
        Last: "hassami",
        City: "Mashhad",
        Age: 12
      },
      tmpPoint: {
        x: null,
        y: null,
        z: null,
      },
      defaultPoint: {
        x: null,
        y: null,
        z: null,
      }
    }
  },
  computed: {},
  watch: {},
  methods: {
    deletePoint(prop) {
      this.points.splice(prop.index, 1);
      this.loadCurve();

    },
    deleteAll() {
      this.points=[];
      this.highchartConfig.series[2].data=[]
      this.highchartConfig.series[1].data=[]

    },
    loadCurve() {
      this.req.points=Array.from(this.points,(v)=>{return{x:Number(v.x),y:Number(v.y),z:Number(v.z)}});
      this.axios.post('http://localhost:9090/nc/plot',
        JSON.stringify(this.req)
      ).then(response => {

        console.log(response);
        let curvePoint =response.data
        this.highchartConfig.series[1].data= Array.from(curvePoint, v => [Number(v.x),Number(v.y)]);
      }, response => {
        // error callback
      });
    },

    addPoint(item) {
      //console.log(this);
      this.points.push(Object.assign({},item))
      this.tmpPoint = Object.assign({}, this.defaultPoint);
      this.loadCurve();
      //console.log(this)
      this.$refs.xInput.focus();
      //console.log(this.req.points)
    },
    goPath(){
      this.req.points=Array.from(this.points,(v)=>{return{x:Number(v.x),y:Number(v.y),z:Number(v.z)}});
      this.highchartConfig.series[2].data=[]
      this.axios.post('http://localhost:9090/nc/go',
        JSON.stringify(this.req)
      ).then(response => {

        console.log(response);
        this.highchartConfig.series[2].data= Array.from(response.data.points, v => [Number(v[1]),Number(v[2])]);
        //console.log("decoding");
        //console.log(JSON.parse(response.data));
        //let curvePoint =response.data
        //this.highchartConfig.series[1].data= Array.from(curvePoint, v => [Number(v.x),Number(v.y)]);
      }, response => {
        // error callback
      });

    }
  },
  props: {},
  created() {},
  mounted() {},
  watch:{
    // req:{
    //   points()
    // },
    snackbar:{
      deep:true,
      message(){
        this.snackbar.toggle=true;
      }
    },
    points(oldval,newval){
      this.highchartConfig.series[0].data = Array.from(this.points, v => [Number(v.x),Number(v.y)]);
      //console.log(Array.from(this.points, v => [v.x,v.y]))
      //console.log(newval);

    },
    // highchartConfig:{
    //   deep: true,
    //   series:{
    //     deep:true,
    //     data(){
    //       console.log("hi");
    //     }
    //     }

    // }
  }

}
export default vm
</script>




